table {
    border-collapse: collapse;
	margin: 0 0 1.5em;
	width: 100%;
}

thread th {
    border-bottom: 2px solid #bbb;
    padding-bottom: 0.5em;
}

td,
th {
    vertical-align: top;
}

th {
    padding: 0.4em;
    text-align: left;
}

tr {
    border-bottom: 1px solid #eee;
}

td {
    padding: 0.4em;
}

th:first-child,
td:first-child {
    padding-left: 0;
}

th:last-child,
td:last-child {
    padding-right: 0;
}